<template>
	<u-navbar
		:title="title"
		:autoBack="true"
		:background="background"
		:border-bottom="false"
		:isFixed="isFixed"
		:isBack="isBack"
		:custom-back="comeback"
	/>
</template>

<script>
	export default {
		props:{
			isBack: {
				type: Boolean,
				default: true
			},
			title: {
				type: String,
				default: ""
			},
			isFixed: {
				type: Boolean,
				default: true
			},
		},
		data() {
			return {
				background: {
					background: "#ffffff",
					color: "#000000"
				}
			}
		},
		methods:{
			comeback() {
				let pages = getCurrentPages() 
				if (pages[pages.length - 2]) {
					//如果有上一页，就返回上一页        
					uni.navigateBack();
				} else {
					//如果没有上一页，则返回首页        
					uni.reLaunch({
						url: "/pages/tabBar/rent"
					});
				}
			}
		}
	}
</script>